body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App .survey-main-btn {
	background-color: #af5f46;
	width: 150px;
	margin: 20px;
	border: none;
	padding: 10px;
}

.App .question-container {
	margin: 20px;
	width: 400px;
}

.App .question-container .input-grp {
	margin-bottom: 5px;
}

.App .question-container .options-text {
	font-size: 15px;
	text-align: left;
	font-weight: bold;
	margin-left: 5px;
	margin-bottom: 5px;
}
